
<template lang="html">
  <div class="grid-container"> 
    <div class="grid-item" v-for="kep in kepek" :key="kep"  >     
      <memory-item :kep=kep :isPair=kep.isPaired :notPairs=notPairs :isFlipped=kep.isFlipped  @pairCheck=pairCheck(kep)></memory-item>
    </div>
  </div>
</template>

<script>
import MemoryItem from "./MemoryItem.vue";

export default {
  components:{
    MemoryItem,
  },
  data(){
    return{
      show: false,
      pos: -1,
      pairs: [],
      notPairs: [],
      found: 0,
      state: "Waiting",
      kepek: [
        {country: "ausztria1", isPaired: false, isFlipped: false},
        {country: "ausztria2", isPaired: false, isFlipped: false},
        {country: "delkorea1", isPaired: false, isFlipped: false},
        {country: "finn1", isPaired: false, isFlipped: false},
        {country: "japan1", isPaired: false, isFlipped: false},
        {country: "nepal1", isPaired: false, isFlipped: false},
        {country: "peru1", isPaired: false, isFlipped: false},
        {country: "sanmarino1", isPaired: false, isFlipped: false},
        {country: "vatikan1", isPaired: false, isFlipped: false},
        {country: "delkorea2", isPaired: false, isFlipped: false},
        {country: "finn2", isPaired: false, isFlipped: false},
        {country: "japan2", isPaired: false, isFlipped: false},
        {country: "nepal2", isPaired: false, isFlipped: false},
        {country: "peru2", isPaired: false, isFlipped: false},
        {country: "sanmarino2", isPaired: false, isFlipped: false},
        {country: "vatikan2", isPaired: false, isFlipped: false}
      ]
    }
  },
  emits: ['end'],
  created(){
    for (let i = this.kepek.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [this.kepek[i], this.kepek[j]] = [this.kepek[j], this.kepek[i]];
    }  
  },
  methods: {
    pairCheck(kep){    
      if (this.pos != this.kepek.map(function(e) { return e.country; }).indexOf(kep.country)){
        this.notPairs.length=0;
        this.pos = this.kepek.map(function(e) { return e.country; }).indexOf(kep.country);      
        this.kepek[this.pos].isFlipped = true;
        this.pairs.push(kep.country);
        if(this.pairs.length == 2)
        { 
          if(this.pairs[0].slice(0, -1) == this.pairs[1].slice(0, -1)){
            this.found++;  
            if (this.found == this.kepek.length/2){
              this.$swal({
                icon: 'success',
                title: `You found all the pairs, congratulations!`,
                confirmButtonText: 'Back to the start page',
                showConfirmButton: true
              }).then(() => this.$emit("end"));
            }else{
              this.$swal({
                icon: 'success',
                title: `Found a new pair! Total: ${this.found}`,
                timer: 2000,
                timerProgressBar: true,
                showConfirmButton: false,
                position: "bottom",
                width: "50%"
              });
            }       
            for (let i = 0; i < this.kepek.length; i++) {
              if (this.kepek[i].country == kep.country){
                this.kepek[i].isPaired = true;
              }          
            }          
          }else{          
            this.notPairs.push(this.pairs[0]);
            this.notPairs.push(this.pairs[1]);    
            this.$swal({
              icon: 'error',
              title: "Maybe next time...wait for flipping back",
              timer: 2000,
              timerProgressBar: true,
              showConfirmButton: false,
              position: "bottom",
              width: "50%"
            });  
            setTimeout(() =>{         
              for (let index = 0; index < 2; index++) {
                let pos = this.kepek.map(function(e) { return e.country; }).indexOf(this.notPairs[index]);
                this.kepek[pos].isFlipped = false;  
              }
            },2250)
          } 
          this.pairs.length = 0;                      
        }        
      }else{
        this.$swal({
          icon: 'success',
          title: `Found a new pair! Total: ${this.found}`,
          timer: 2000,
          timerProgressBar: true,
          showConfirmButton: false,
          position: "bottom",
          width: "50%"
        });
      }
    },    
  },
};
</script>
<style scoped>
.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto;  
  justify-items: center;
  align-items: center;
  width:50%;
}
.grid-item{
  display: inline-block;
  width: 75%;
}
</style>
