<template>    
  <div v-if="!selected">
    <main-header></main-header>
    <button class="startBtn" :class=this.btnClass @click="start">START GAME!</button>
  </div> 
  <div class="grid animate__animated animate__zoomIn" v-else>    
    <memory-table :gridsize=value :kepek=kepek @end=end></memory-table>    
  </div>
  <main-footer></main-footer>
</template>

<script>
import MemoryTable from './components/MemoryTable.vue'
import MainHeader from './components/MainHeader.vue'
import MainFooter from './components/MainFooter.vue'
import 'animate.css';

export default {
  name: 'App',
  components: {
    MemoryTable,
    MainHeader,
    MainFooter
  },
  data(){
    return{
      selected: false,
      value: 4,
      btnClass: ""
    }
  },
  methods: {
    start(){
      this.btnClass = "animate__animated animate__bounceOut";
      setTimeout(()=>{
        this.selected = true;           
      }, 1500)
    },
    end(){
      this.selected = false;
      this.btnClass = "";
    }
  }
}
</script>

