<template lang="">      
  <img class="grid-item" v-if="!this.flipped" src="assets/q.png" @click="handleClick()">
  <img class="grid-item" v-else :src="'assets/'+kep.country.slice(0,-1)+'.png'" @click="handleClick()" :alt=kep>
</template>
<script>
export default {
    emits: ['pairCheck'],
    props: ["kep", "isPair", "notPairs","isFlipped"],
    data(){
      return{
        flipped: this.isFlipped
      }
    },
    methods:{
        handleClick(){
          if(!this.isPair){
            this.flipped = true;
            if(this.flipped){
              this.$emit("pairCheck", {kep: this.kep})
            }
          }            
      }    
    },
    updated() {
      this.flipped = this.isFlipped;        
    },
}
</script>
<style scoped>
.grid-item {
  box-shadow: 10px 10px 10px #424040;
  width: 95%;
  margin: 5px;
}
</style>